import "./styles.css";
import { useState, useEffect } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./components/Header";
import About from "./components/About";
import Footer from "./components/Footer";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";

export default function App() {
  const [showAddTask, setShowAddTask] = useState(false);
  //const [tasks, setTasks] = useState([]);

  //useEffect(() => {
  //  const fetchTasks = async () => {
  //const res = await fetch(/tasks/)
  //const data = await res.json()
  //console.log(data)
  //  };

  //  fetchTasks();
  //}, []);

  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: "CoÃ§ar o Saco",
      day: "Hoje",
      reminder: true
    },
    {
      id: 2,
      text: "Tomar Banho",
      day: "Amanha",
      reminder: true
    },
    {
      id: 3,
      text: "Assistir Anime",
      day: "Ontem",
      reminder: true
    }
  ]);

  // Delete Task
  const deleteTask = (id) => {
    setTasks(tasks.filter((task) => task.id !== id));
    //setTasks(tasks.find((task) => task.id !== id));
    //console.log(tasks.filter((task) => task.id == id))
    //console.log(tasks.find((task) => task.id == id))
  };

  // Toggle Reminder
  const toggleReminder = (id) => {
    setTasks(
      tasks.map((task) =>
        task.id === id ? { ...task, reminder: !task.reminder } : task
      )
    );
  };

  // Add Task    <Header onAdd={() => setShowAddTask(!showAddTask)} />
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 10000) + 1;
    const newTask = { id, ...task };
    setTasks([...tasks, newTask]);
  };

  return (
    <Router>
      <div className="container">
        <Header
          onAdd={() => setShowAddTask(!showAddTask)}
          showAdd={showAddTask}
        />

        <Route
          path="/"
          exact
          render={(props) => (
            <>
              {showAddTask && <AddTask onSaveButtonClick={addTask} />}
              {tasks.length > 0 ? (
                <Tasks
                  tasks={tasks}
                  onDelete={deleteTask}
                  onToggle={toggleReminder}
                />
              ) : (
                "No tasks to show"
              )}
            </>
          )}
        />

        <Route path="/about" component={About} />
        <Footer />
      </div>
    </Router>
  );
}
